@import "../../base";
@import "../../mixins";

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-30px);
    -ms-transform: translateY(-30px);
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -moz-transform: translateY(-15px);
    -ms-transform: translateY(-15px);
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}

.hero_container {
  width: 100%;
  margin-top: $nav-height;

  .hero_bg {
    height: inherit;
    width: 100%;
    display: flex;
    flex-direction: column;
    max-width: 1100px;
    margin: 0 auto;
    color: white;
    align-items: center;
    padding: 100px 15px 150px 15px;
    justify-content: center;
    position: relative;

    .hero_header {
      font-family: Montserrat;
      text-transform: uppercase;
      font-weight: 400;
      font-size: clamp(1rem, 10vw, 2.5rem);
    }

    .hero_Subheader {
      font-size: clamp(1rem, 2.5vw, 1.2rem);
      font-family: "Montserrat";
      font-weight: 400;
      opacity: 0.7;
      text-align: center;
      max-width: 800px;
      margin: 1rem 0;
      width: 100%;
      line-height: 1.5rem;
    }

    .hero_btn {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-top: 1.5rem;
      gap: 1rem;

      .project_btn,
      .resume_btn {
        @include ButtonStyle();

        img {
          width: 25px;
          margin-right: 0.5rem;
        }
      }

      .resume_btn {
        background-color: #3d5af1d1;
      }
    }

    .down_arrow {
      font-size: 2rem;
      position: absolute;
      color: white;
      opacity: 0.5;
      cursor: pointer;
      bottom: 2rem;
      animation: bounce 2s infinite;
    }
  }
}
